.header .navbar {
  margin: 0;
}
.header-bottom {
  -webkit-box-shadow: 0 3px 3px -3px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 0 3px 3px -3px rgba(0, 0, 0, 0.3);
  box-shadow: 0 3px 3px -3px rgba(0, 0, 0, 0.3);
}
.site-logo {
  -webkit-flex: 0 0 auto;
  flex: 0 0 auto;
}
.site-logo a {
  display: inline-block;
  line-height: 64px;
}
.site-menu {
  -webkit-flex: 1;
  flex: 1;
}
.site-menu {
  ul {
    padding: 0;
    margin: 0;

    li {
      display: inline-block;
      padding: 22px 0;
      position: relative;
      text-align: left;

      a {
        padding: 0 15px;
        text-shadow: none;
        display: block;
        &:hover {
          background-color: transparent;
          color: $color-hover;
        }
      }
      &:last-child {
        a {
          padding-right: 0;
        }
      }
      .sub-menu {
        min-width: 160px;
        border-top: 1px solid $color-hover;
        background-color: $border-color;
        position: absolute;
        top: 120%;
        left: 0;
        z-index: -1;
        visibility: hidden;
        opacity: 0;
        @include transition(all 0.4s ease-out);

        li {
          padding: 8px 0;
          display: block;

          a {
            white-space: nowrap;
          }
          .sub-menu {
            left: 90%;
            top: -1px;
          }
          &:hover {
            > .sub-menu {
              left: 100%;
            }
          }
        }
      }
      &:hover {
        > .sub-menu {
          top: 100%;
          z-index: 999;
          opacity: 1;
          visibility: visible;
        }
      }
    }
  }
}
.icon_menu_item_mobile {
  display: none;
  position: absolute;
  right: 15px;
  top: 15px;
  width: 12px;
  height: 20px;
  font-size: 14px;
  cursor: pointer;

  &:after {
    content: "\f196";
    display: inline-block;
    font-family: "fontawesome";
    font-style: normal;
    font-weight: normal;
  }
  &.icon_menu_item_mobile_active {
    &:after {
      content: "\f147";
    }
  }
}